import React from 'react';
import { Copy, Mail, Phone } from 'lucide-react';

const ContactDetails = () => {
  return (
    <div className="bg-gray-900/20 backdrop-blur-md border border-gray-700/30 rounded-lg p-4 mb-4">
      <div className="flex items-center justify-between mb-3">
        <span className="text-gray-400 text-sm">Contact</span>
        <Copy className="w-4 h-4 text-gray-400 hover:text-white cursor-pointer" />
      </div>
      <div className="flex items-center mb-2">
        <Mail className="w-4 h-4 text-gray-400 mr-2" />
        <span className="text-white text-sm">martin@trymartin.com</span>
      </div>
      <div className="flex items-center">
        <Phone className="w-4 h-4 text-gray-400 mr-2" />
        <span className="text-white text-sm">+18559108323</span>
        <Copy className="w-4 h-4 text-gray-400 ml-auto hover:text-white cursor-pointer" />
      </div>
    </div>
  );
};

export default ContactDetails;