import React, { useState } from 'react';
import { Search, Plus, MoreHorizontal } from 'lucide-react';

const TodoList = () => {
  const [searchTerm, setSearchTerm] = useState('');
  
  const todos = [
    { id: 1, text: "From Martin: Banner Ad Designer opportunity at Wuzzon BV for", priority: "P2", date: "Aug 4" },
    { id: 2, text: "problems of software team because of stack problem", date: "Aug 4" },
    { id: 3, text: "discussion on software about software etc products", date: "Aug 4" },
    { id: 4, text: "discussion on usa calling", date: "Aug 4" },
    { id: 5, text: "have a meeting with fadi and hamza on everything", date: "Aug 4" }
  ];

  return (
    <div className="bg-gray-900/20 backdrop-blur-md border border-gray-700/30 rounded-lg p-4 mb-4">
      <div className="flex items-center justify-between mb-4">
        <h2 className="text-white font-medium">To-dos</h2>
        <div className="flex items-center gap-2">
          <MoreHorizontal className="w-4 h-4 text-gray-400" />
        </div>
      </div>
      
      <div className="relative mb-4">
        <Search className="w-4 h-4 text-gray-400 absolute left-3 top-1/2 transform -translate-y-1/2" />
        <input
          type="text"
          placeholder="Search to-dos"
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="w-full bg-gray-800/50 border border-gray-600/50 rounded-md pl-10 pr-4 py-2 text-sm text-white placeholder-gray-400 focus:outline-none focus:border-gray-500"
        />
      </div>

      <div className="flex items-center justify-between mb-3">
        <button className="flex items-center text-white text-sm bg-gray-800/50 px-3 py-1 rounded">
          All
        </button>
        <div className="text-gray-400 text-xs">raliather303@gmail.com</div>
      </div>

      <div className="space-y-2">
        {todos.map((todo) => (
          <div key={todo.id} className="flex items-start gap-3 p-2 hover:bg-gray-800/30 rounded-md cursor-pointer">
            <input type="checkbox" className="mt-1 w-4 h-4 bg-transparent border border-gray-600 rounded" />
            <div className="flex-1">
              <p className="text-white text-sm">{todo.text}</p>
              <div className="flex items-center gap-2 mt-1">
                {todo.priority && (
                  <span className="text-yellow-500 text-xs bg-yellow-500/20 px-2 py-0.5 rounded">
                    {todo.priority}
                  </span>
                )}
                <span className="text-gray-400 text-xs">{todo.date}</span>
              </div>
            </div>
            <Plus className="w-4 h-4 text-gray-400 hover:text-white" />
          </div>
        ))}
      </div>
    </div>
  );
};

export default TodoList;